@import url(https://fonts.googleapis.com/css?family=Lilita+One&display=swap);
body {
  background: url("https://images3.alphacoders.com/982/982056.jpg");
  background-size: cover;
  margin: 15px auto;
  font-family: 'Lilita One', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo{
    font-size: 60px;
    color: orange;
    text-align: center;
    margin: 25px auto;
    font-family: 'Lilita One', cursive;
    -webkit-text-stroke-width: 3px;
    -webkit-text-stroke-color: white;
}

.container {
    min-height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 23px;
    color: white;
    text-align: center;
    margin: 25px auto;
}

h1,
h2 {
    margin: 10px;
}

.answer {
    border: 7px solid darkblue;
    opacity: 75%;
    border-radius: 20px;
    box-shadow: 0 17px 7px -7px black;
    padding: 5px 10px 5px 45px;
    width: 610px;
    text-align: left;
    margin-top: 80px;
    position: relative;
    font-size: 22px;
    cursor: pointer;
    -webkit-transition: ease-in-out, width 0.35s ease-in-out;
    transition: ease-in-out, width 0.35s ease-in-out;
}

.answer.selected {
    color: #fff;
    background-color: orange;
    border: 10px solid #fff;
}

.answer:hover {
    color: #fff;
    background-color: darkblue;
    border: 10px solid #fff;
}

.answer:focus {
    outline: none;
}
.results{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 34px;
  background-color: black;
  height: 100%;
  width: 900px;
  text-align: center;
  opacity: 77%;
  border-radius: 77px;
}

.results span.correct {
    color: lawngreen;
    font-size: 40px;
}

.results span.failed {
    color: red;
    font-size: 40px;
}

.error {
    color: greenyellow;
    -webkit-animation: blinker 1s linear infinite;
            animation: blinker 1s linear infinite;
    font-size: 30px;
}
@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.letter {
    color: orangered;
    width: 30px;
    position: absolute;
    left: 0;
    text-align: center;
    height: 28px;
    top: 0;
    padding: 5px;
    text-transform: uppercase;
}

.btn {
    color: orangered;
    background-color: #f4f4f4;
    padding: 10px 50px;
    text-transform: uppercase;
    font-size: 18px;
    margin-top: 20px;
    cursor: pointer;
}

.btn-primary {
    color: #fff;
    background-color: darkblue;
    border: 5px solid white;
    border-radius: 25px;
    font-weight: bold;
    box-shadow: 0 17px 7px -7px black;
    opacity: 100%;
}

